	module.exports = {"data":{"markdownRemark":{"html":"<h3>Описание:</h3>\n<p>Данное упражнение сосредоточено на проработку широчайшей мышцы спины и требует только наличие турника. Можно подтягиваться как к груди, так и за голову. При подтягивании за голову в работе участвует еще задняя дельта.</p>\n<p><strong>Внимание:</strong> вариант подтягиваний за голову сильно нагружает мышцы плечевого пояса. Поэтому, если у Вас слабые мышцы плеч, Вам лучше выполнять подтягивания к груди.</p>\n<h3>Порядок выполнения:</h3>\n<ol>\n<li>Исходное положение: возьмитесь за перекладину турника широким хватом сверху.</li>\n<li>На выдохе - выполните подтягивание, пока перекладина не окажется за шеей (если подтягиваетесь к груди, то коснуться перекладины верхней частью груди). Задержитесь в верхней точке. Постарайтесь максимально напрягать мышцы спины в верхней точке упражнения.\n</li>\n<li>На выдохе - медленно вернитесь в исходное положение.</li>\n<li>Проделайте необходимое количество повторений.</li>\n</ol>\n<h3>Видео-инструкция:</h3>\n<iframe width=\"100%\" height=\"550rem\" src=\"https://www.youtube.com/embed/RKEWzx_7hBg\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" allowfullscreen></iframe>","frontmatter":{"path":"/exercise/podtyagivania-shirokim-hvatom","efficiency":"10","title":"Подтягивания широким хватом","image":"/static/2-01-0.jpg","image_2":"","image_3":"","image_4":"","filterby":"back","date":"April 12, 2017","injury":"3","bodypart":"Спина","filtertype":"base","type":"Базовое","filterinventory":"ownweight","inventory":"Собственный вес","filterplace":null}}},"pathContext":{}}

